@import url("https://fonts.googleapis.com/css?family=Bangers");
@import url("https://fonts.googleapis.com/css?family=Cookie");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
.accordion-container {
  max-width: 100%;
  max-height: 544px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  position: relative;
  overflow: hidden;
}
@media (max-width: 768px) {
  .accordion-container {
    max-height: none;
  }
}
.accordion-container .accordion {
  width: 9999px;
  height: 100%;
  position: relative;
}
.accordion-container .accordion .accordion-item-container {
  height: 100%;
  float: left;
  top: 0;
  overflow: hidden;
}
.accordion-container .accordion .accordion-item-container:hover {
  cursor: pointer;
  opacity: .9;
}
.accordion-container .accordion .accordion-item-container .accordion-item-content {
  position: relative;
  height: 100%;
}
.accordion-container .accordion .accordion-item-container .accordion-item-content .accordion-item-text {
  position: absolute;
  top: 38%;
  width: 50%;
  font-size: 60px;
}
@media (max-width: 1087px) {
  .accordion-container .accordion .accordion-item-container .accordion-item-content {
    width: calc(100% - 60px);
  }
}
.accordion-container .accordion .accordion-item-container .accordion-item-content img {
  width: 100%;
  max-width: 100%;
}
.accordion-container .accordion .accordion-item-container .accordion-item-content > div {
  width: 50%;
}
.accordion-container .accordion .accordion-item-container div {
  float: left;
}
.accordion-container .accordion .accordion-item-container div > div:last-of-type {
  background-color: #ccc;
  padding-bottom: 9999px;
  margin-bottom: -9999px;
}
.accordion-container .accordion .accordion-item-container div div.close {
  float: right;
  padding-right: 25px;
  padding-top: 5px;
  font-size: 24px;
  position: relative;
  z-index: 1000;
  padding-bottom: 0;
  margin-bottom: 0;
}
.accordion-container .accordion .accordion-item-container div div.close:after {
  content: "X";
  font-size: 13px;
  padding: 3px 8px;
  background-color: #333;
  border-radius: 50%;
  color: #fff;
  font-family: 'Bangers', cursive;
  display: block;
}
.accordion-container .accordion .accordion-item-container div div.close:hover {
  opacity: .8;
  cursor: pointer;
}
.accordion-container .accordion .accordion-item-container .accordion-item-text {
  font-family: 'Bangers', cursive;
  color: #fff;
  text-shadow: 5px 5px 0 rgba(0, 0, 0, 0.7);
  transition: all 100ms ease-in;
  transform: skew(-51deg, 52deg) rotate(-53deg) translateX(34%) scale(0.6);
}
.accordion-container .accordion .accordion-item-container .accordion-item-text span {
  position: relative;
  z-index: 30;
}
.accordion-container .accordion .accordion-item-container .accordion-item-text .star-bg {
  background: tomato;
  width: 150px;
  height: 150px;
  position: relative;
  z-index: 10;
  text-align: center;
  transition: all 100ms ease-in;
  transform: rotate(10deg);
}
.accordion-container .accordion .accordion-item-container .accordion-item-text .star-bg::before {
  content: "";
  position: absolute;
  top: 110px;
  left: 90px;
  height: 50px;
  width: 50px;
  background: tomato;
  transform: rotate(115deg);
}
.accordion-container .accordion .accordion-item-container .accordion-item-text .star-bg::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 80px;
  width: 80px;
  background: tomato;
  transform: rotate(115deg);
}
.accordion-container .accordion .accordion-item-container.active:hover {
  cursor: default;
  opacity: 1;
}
.accordion-container .accordion .accordion-item-container.active .accordion-item-text {
  transform: skew(-51deg, 52deg) rotate(-53deg) translateX(39%) scale(1);
}
.accordion-container .accordion .accordion-item-container.active .star-bg {
  top: -60%;
  left: -2%;
  background: tomato;
  width: 150px;
  height: 150px;
  position: absolute;
  text-align: center;
  transition: all 100ms ease-in;
  transform: rotate(20deg) skew(-11deg, 1deg);
}
.accordion-container .accordion .accordion-item-container.active .star-bg::before {
  content: "";
  position: absolute;
  top: 110px;
  left: 90px;
  height: 50px;
  width: 50px;
  background: tomato;
  transform: rotate(135deg) skew(-51deg, 52deg);
}
.accordion-container .accordion .accordion-item-container.active .star-bg::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 80px;
  width: 80px;
  background: tomato;
  transform: rotate(135deg) skew(-51deg, 52deg);
}
.accordion-container .touch-icon {
  display: block;
  position: absolute;
  opacity: 0;
  right: 60%;
  top: 30%;
  animation-duration: 1s;
  animation-name: touchicon;
}
.accordion-container .touch-icon.icon-swipe-left::before {
  color: #fff;
  font-size: 120px;
}
@media (min-width: 768px) {
  .accordion-container .touch-icon {
    display: none;
  }
}
.accordion-container ul.acc-xs-nav {
  list-style: none;
  padding-left: 0;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  display: inline-flex;
  flex-direction: row;
  justify-content: center;
}
.accordion-container ul.acc-xs-nav li {
  width: 20px;
  height: 20px;
  background-color: #ccc;
  border-radius: 50%;
  text-align: center;
}
.accordion-container ul.acc-xs-nav li.active {
  background-color: #666;
}
.accordion-container ul.acc-xs-nav li a {
  font-size: 14px;
  text-decoration: none;
  color: #000;
  display: block;
  margin-top: 2px;
}
.accordion-container ul.acc-xs-nav li:hover {
  cursor: pointer;
}
.accordion-container ul.acc-xs-nav li:not(:last-child) {
  margin-right: 10px;
}
@media (min-width: 768px) {
  .accordion-container ul.acc-xs-nav li {
    display: none;
  }
}
@keyframes touchicon {
  0% {
    opacity: 0;
    right: 20%;
  }
  10% {
    opacity: 1;
    transform: scale(1);
    right: 20%;
  }
  15% {
    opacity: 1;
    transform: scale(0.9);
  }
  38% {
    opacity: 1;
    transform: scale(0.9);
  }
  40% {
    opacity: 1;
    right: 20%;
  }
  41% {
    opacity: 1;
    transform: scale(0.9);
    right: 20%;
    filter: blur(0px);
  }
  80% {
    filter: blur(5px);
    opacity: 1;
    right: 60%;
    transform: scale(0.9);
  }
  81% {
    filter: blur(0px);
    right: 60%;
  }
  100% {
    filter: blur(0px);
    transform: scale(0.6);
    opacity: 1;
  }
}
.acc-small-screen-check {
  display: block;
}
@media (min-width: 768px) {
  .acc-small-screen-check {
    display: none;
  }
}
.clearfix::after,
.clearfix::before {
  display: table;
  content: " ";
}
.clearfix::after {
  clear: both;
}
